.btn-ripple:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  border-color: inherit;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  opacity: 1;
  animation-name: rippleanim;
  animation-duration: 1.2s;
  animation-iteration-count: infinite;
  animation-timing-function: cubic-bezier(0.4, 0, 0.3, 1);
  animation-delay: 1s;
}

@keyframes rippleanim {
  0% {
    opacity: 1;
    box-shadow: 0 0 0 0 rgba(23, 162, 184, 0.9);
  }
  100% {
    opacity: 0;
    box-shadow: 0 0 0 15px rgba(23, 162, 184, 0.4);
  }
}
@-webkit-keyframes rippleanim {
  0% {
    opacity: 1;
    background: rgba(23, 162, 184, 0.9);
    transform: scale(0);
  }
  100% {
    opacity: 0;
    background: rgba(23, 162, 184, 0.4);
    transform: scale(1.5);
